<template>
	<view class="box">
		<view class="tabs">
			<view class="search">
				<uni-icons type="search" color="#999999" size="24" @click="handleSearch"></uni-icons>
				<input type="text" v-model="keyword" class="inputs" placeholder="搜索" />
			</view>
			<view class="top-box">
				<swiper-tab :tabs="tabs" :current="currentTab" :activeColor="bgcolor" :barColor="bgcolor"
					:background="background" :color="bgcolor" :fontSize="28" @change="onTabCgnage" :overwidth="10">
				</swiper-tab>
			</view>
		</view>
		<scroll-view scroll-y class="order" @scrolltolower="handleLower">
			<order-harvest v-for="(item,index) in harvestList" :key="index" :order="item"
				@onCancelOrder="handleCancelOrder" @onPayNow="handlePayNow">
			</order-harvest>
			<view v-if="harvestList.length === 0" class="nono-list">
				————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import SwiperTab from '@/frame/components/swiper-tab/index.vue'
	import HarvestStatusEnum from '@/utils/enum.ts'
	import OrderHarvest from './components/order/index.vue'
	import {
		PostOrderHarvestAjax,
		GetConfirmOrderHarvestAjax
	} from '@/apis/api/modules/user-center'

	export default {
		components: {
			SwiperTab,
			OrderHarvest
		},
		data() {
			return {
				bgcolor: '#fff',
				background: '#00D08F',
				currentTab: 0,
				tabs: [{
					text: '全部',
					// value: HarvestStatusEnum.全部
				}, {
					text: '待发货',
					// value: WorkStatusEnum.待付款
				}, {
					text: '待收货',
					// value: HarvestStatusEnum.待收货
				}, {
					text: '待评价',
					// value: HarvestStatusEnum.待评价
				}, {
					text: '已完成',
					// value: HarvestStatusEnum.已完成
				}],
				lower: true,
				keyword: null,
				status: -1,
				pageIndex: 1,
				harvestList: []
			}
		},
		onLoad(e) {
			this.status = Number(e.index)
			this.currentTab = Number(Number(e.index) + 1)
		},
		onShow() {
			this.getDate()
		},
		// onReachBottom() {
		// 	console.log('PageIndex', this.PageIndex)
		// 	this.pageIndex += 1;
		// 	this.getDate();
		// },
		methods: {
			handleSearch() {
				this.getDate()
			},
			onTabCgnage(e) {
				this.pageIndex = 1
				this.status = e - 1
				// this.lower = true
				this.getDate()
			},
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.pageIndex = this.pageIndex + 1

				const result = await PostOrderHarvestAjax({
					Keyword: this.keyword || null,
					Status: this.status,
					PageIndex: this.pageIndex,
					PageSize: 4,
					token: true
				})
				if (result.Result.List.length > 0) {
					this.lower = true
					this.harvestList.push(...result.Result.List)
					return false
				}

				this.lower = false
			},
			async getDate() {
				const result = await PostOrderHarvestAjax({
					Keyword: this.keyword || null,
					Status: this.status,
					PageIndex: this.pageIndex,
					PageSize: 4,
					token: true
				})
				// this.harvestList = [...this.harvestList,...result.Result.List]
				if (result.Result.List.length === 0) {
					this.lower = false
				}
				// this.lower = false
				this.harvestList = result.Result.List
			},
		}
	}
</script>

<style scoped lang="scss">
	.box {
		height: 100%;
		display: flex;
		flex-direction: column;
		background: #F7F7F7;
	}

	.nono-list {
		text-align: center;
		color: #c1c1ba;
		padding: 30rpx 0;
	}

	.tabs {
		padding: 16rpx 30rpx 0;
		background: #00D08F;

		.search {
			display: flex;
			justify-content: start;
			align-items: center;
			padding-left: 22rpx;
			background: #FFFFFF;
			border-radius: 34rpx;
			// margin-bottom: 26rpx;

			.inputs {
				padding-left: 24rpx;
				font-size: 28rpx;
				font-weight: 400;
				color: #979DA6;
			}
		}
	}

	.order {
		padding: 30rpx;
		flex: 1;
		overflow: auto;
		box-sizing: border-box
	}
</style>
